import * as express from 'express';
import {IUserMap} from "../interface/login.interface";

const arboxRouter = express.Router();

const router = (users: IUserMap) => {
    arboxRouter.get('/register-user', (req, res) => {
        const {user} = req.body as any;
        const user = users.get(user.id)
        if (!user) {
            users.set(user.id, user);
        }
    });

    arboxRouter.get('/', (req, res) => {
        res.json({helo: 1});
    });

    arboxRouter.post('/', (req, res) => {
        const {items, customerID} = req.body;
        res.json({helo: 1});

    });
    return arboxRouter;
};
export default router;